import {
	getRasumePreview,
} from '@/api/job'
import {
	$wuxToast,
} from '../../wxcomponents/wux/index'
export default {
	data() {
		return {
			detail: {
				info: {
					marital: 0,
					type: 0
				}
			},
		}
	},
	onShow() {
		this.getRasumePreview()
	},
	methods: {
		getRasumePreview() {
			getRasumePreview().then(response => {
				let r = response.data
				if (response.code === 20000) {
					this.detail = r.detail
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
				}
			})
		}
	}
}
